ul{
	list-style: none;
	padding-left: 0px;
	margin-top: 0px;
	margin-bottom: 0px;
}
h1,h2,h3,h4,h5,h6{
	margin: 0px;
}
/*菜单*/
.menu{
	background-color: #2d2e2d;
	-webkit-transition:height .35s ease;
	-o-transition:height .35s ease;
	transition:height .35s ease;
	position: fixed;
    width: 100%;
    z-index: 99;
    color: #fff;
    font-weight: 400;
    overflow: hidden;
}
.menuList .items{
	border-bottom: 1px solid #444444;
}
/*首页主标题副标题*/
.app-title{
	text-align: center;
	color: #333333;
}
.app-sub-title{
	text-align: center;
	color: #999999;
} 
/*产品详情*/
.productDescList .items{
	background-color: #fff;
	border-bottom: 2px solid #e5e5e5;
}
.productDescList .title{
	color: #333333;
}
.productDescList .detail{
	color: #666666;
}
.productDescList .icon{
	color: #37bdb4;
	position: relative;
}
/*解决方案*/
.solution{
	background-color: #fff;
	padding: 1px;
}
.solutionList .items{
	width: 100%;
	position: relative;
	color: #fff;
	background-color: rgba(51,51,51,0.8);
	-ms-flex-align:center;
	    align-items:center;
	display: -ms-flexbox;
	display: flex;
	margin-bottom: 4px;
	text-align: center;
	-ms-flex-pack:center;
	    justify-content:center;
	overflow: hidden;
}
.solutionList .items img{
	position: absolute;
	width: 100%;
	left: 0px;
	top: 0px;
}
.solutionList .title{
	position: relative;
}
.solutionList .title .icon{
	padding-bottom: 10px;
	border-bottom: 1px solid #fff;
}


